import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '',
    name: 'start-page',
    component: () => import('../views/StartPage.vue'),

  },
  {
    path: '/poll',
    name: 'poll',
    component: () => import('@/game'),
  },
  {
    path: '/policy',
    name: 'policy',
    component: () => import('@/views/PolicyPD.vue'),
  },
  {
    path: '/developers',
    name: 'developers',
    component: () => import('@/views/TheDevelopers.vue'),
  },
  {
    path: '/methods',
    name: 'methods',
    component: () => import('@/views/TheMethods.vue'),
  },
  {
    path: '*',
    redirect: '/',
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes,
});

export default router;
