<template lang="pug">
  v-app#app
    v-content
      .generic-background
        template(v-if="true || isAuthorized")
          router-view.router-view
        template(v-else)
          v-container(fill-height)
            v-layout(row justify-center align-center)
              v-flex
                h1.text-uppercase Вы не авторизованы
                p Пройдите по
                  a(:href="origin")  ссылке
                  |  и убедитесь что вы авторизованы в личном кабинете сертификата
    //- the-footer(v-show="$route.name !== 'poll'")
</template>

<script>
import { mapGetters } from 'vuex';

import TheFooter from './components/TheFooter';


const getOrigin = () => window.location.origin;

export default {
  components: { TheFooter },
  data() {
    return {
      origin: getOrigin(),
    };
  },
  computed: {
    ...mapGetters(['isAuthorized']),
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.$store.dispatch('INIT_CERTIFICATE');
    },
  },
};
</script>
