import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';

import Service from '@/services';

import ResultClasses from '@/data/classes';

Vue.use(Vuex);

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    currentSection: 1,
    resultClasses: Object.assign({}, ResultClasses),
    user: {
      name: null,
      gender: null,
    },
    certificate: null,
    isAuthorized: false,
  },
  getters: {
    getCurrentSection(state) {
      return state.currentSection;
    },
    getResultClasses(state) {
      return state.resultClasses;
    },
    getUser(state) {
      return state.user;
    },
    isAuthorized(state) {
      return state.isAuthorized;
    },
    getCertificate(state) {
      return state.certificate;
    },
  },
  actions: {
    SET_CURRENT_SECTION({ commit }, { currentSection }) {
      commit('SET_CURRENT_SECTION', { currentSection });
    },
    WRITE_SCORE({ commit }, { classes }) {
      commit('WRITE_SCORE', { classes });
    },
    SET_USER({ commit }, user) {
      commit('SET_USER', user);
    },
    RESET_TEST({ commit }) {
      commit('RESET_TEST');
    },
    INIT_CERTIFICATE({ commit }) {
      Service.getOwn()
        .then(({ data }) => {
          if (data.data.role !== 'certificate') return;
          commit('SET_CERTIFICATE', data.data);
        })
        .catch((err) => {
          if (err && err.response.status === 401) {
            console.log('You need to authorize');
          }
        });
    },
  },
  mutations: {
    SET_CURRENT_SECTION(state, { currentSection }) {
      state.currentSection = currentSection;
    },
    WRITE_SCORE(state, { classes }) {
      classes.forEach((item) => {
        state.resultClasses[item.class].value += item.score;
      });
    },
    SET_USER(state, user) {
      Object.assign(state.user, user);
    },
    RESET_TEST(state) {
      state.currentSection = 1;
      state.resultClasses = Object.assign({}, ResultClasses);
      state.user = {
        name: null,
        gender: null,
      };
    },
    SET_CERTIFICATE(state, certificate) {
      state.certificate = certificate;
      state.isAuthorized = true;
    },
  },
});
